<template>
  <div class="vx-settings">
    <vx-header :showback="1" :title="$t('osd.osdMng')"></vx-header>
    <img ref="novastar" src="../../assets/common/novastar.png" v-show="0" />
    <div class="vx-container" @click.stop="showOperators = false;selectedPic= null;">
      <div class="piclist">
        <div class="add-box">
          <div class="pic add" @click="addPic"></div>
        </div>
        <div class="pic-box" v-for="(img,index) in picList" @click.stop="checkPic(index)">
          <div class="pic" :class="{'selected':selectedPic === index,'osd':img.type === 0 ,osdtext:img.type !== 0}">{{img.id + 1}}</div>
        </div>
      </div>
      <div class="operators" v-if="showOperators">
        <div class="btn edit" @click.stop="editOSD"></div>
        <div class="btn del" @click.stop="deleteSheet=true;showOperators=false;"></div>
      </div>
    </div>
    <vx-actionsheet :actions="delaction" :visible="deleteSheet" @cancel="deleteSheet=false" :cancel-text="$t('common.cancel')"></vx-actionsheet>
    <vx-actionsheet :actions="addOsdaction" :visible="addSheet" @cancel="closePhotoSheet" :cancel-text="$t('common.cancel')"></vx-actionsheet>
    <vx-imgupload ref="imgupload" @go="goNext"></vx-imgupload>
    <div v-if="addSheet||deleteSheet" class="v-modal" style="z-index:99" @click.stop="addSheet = false;deleteSheet=false;"></div>
  </div>
</template>

<script>
  import htp from "../../api/http";
  import urls from '../../api/apiurl';
  import { mapMutations } from 'vuex';
  import { Toast } from 'mint-ui';
  import { dataURLtoFile } from '../../utils';

  export default {
    data() {
      return {
        picList: [],
        selectedPic: null,
        showOperators: false,
        deleteSheet: false,
        delaction: [],
        addSheet: false,
        addOsdaction: []
      };
    },
    mounted() {
      this.getOsdList();
      this.delaction = [{
        name: this.$t('osd.delImage'),
        method: this.deleteOsdPic
      }];
    },
    methods: {
      ...mapMutations(['setUploadImg']),
      getOsdList() {
        htp.get(urls.getValidOsdList).then(res => {
          if(res.status === 10000000) {
            this.picList = res.data.pictureList;
          }
        });
      },
      addPic() {
        this.addSheet = true;
        this.selectedPic = null;
        this.addOsdaction = [{
          name: this.$t('osd.addImage'),
          icon: 'addpic',
          method: this.selectPhoto
        }, {
          name: this.$t('osd.addText'),
          icon: 'addtext',
          method: this.addtext
        }];
      },
      selectPhoto() {
        this.addSheet = false;
        this.$refs.imgupload.createEventBroadcast();
      },
      goNext() {
        this.$router.push({ name: 'editosd' });
      },
      addtext() {
        this.addSheet = false;
        if(this.selectedPic != null) {
          this.$router.push({ name: 'osdtext', params: { id: this.picList[this.selectedPic].id } });
        } else {
          this.$router.push({ name: 'osdtext' });
        }
      },
      closePhotoSheet() {
        this.addSheet = false;
      },
      checkPic(index) {
        this.selectedPic = index;
        this.showOperators = true;
      },
      editOSD() {
        let currentPic = this.picList[this.selectedPic];
        if(currentPic.type === 0) {
          if(offline) {
            this.setUploadImg({ type: 0, src: dataURLtoFile(this.$refs.novastar.src, 'novastar') });
          } else {
            this.setUploadImg({ size: currentPic.width * currentPic.height, id: currentPic.id, name: currentPic.id + '.png', type: 1, src: window.apihost + '/downloadOsdPicture?{id=' + currentPic.id + '}' });
          }
          this.goNext();
        } else {
          this.addtext();
        }
      },
      cancel() {
        this.deleteSheet = false;
        this.showOperators = true;
      },
      deleteOsdPic() {
        let index = this.selectedPic;
        htp.post(urls.deleteOsdPicture, {
          id: this.picList[this.selectedPic].id,
          type: this.picList[this.selectedPic].type
        }).then(res => {
          this.picList.splice(index, 1);
          if(res.status === 10000000) {
            Toast(this.$t('osd.deleteSuccess'));
            this.deleteSheet = false;
          }
        });
      }
    }
  };
</script>

<style lang="less" scoped>
  .vx-container {
    padding-top: 30px;
    .piclist {
      padding: 0 25px;
      height: auto;
      display: flex;
      flex-wrap: wrap;
      .add-box,
      .pic-box {
        margin-bottom: 30px;
        .add {
          background: url('../../assets/common/big-add.png') no-repeat;
          background-size: 85px 85px;
          background-position: center;
        }
        .pic {
          width: 196px;
          height: 196px;
          line-height: 196px;
          color: #ddd;
          font-size: 12px; /*no*/
          margin: 0 auto;
          border: 1px solid #121116; /*no*/
          border-radius: 6px;
          background-color: #40434c;
          overflow: hidden;
        }
        .selected {
          border: 1px solid #fd7b00; /*no*/
        }
        .osd {
          background: url('../../assets/common/osd.png') no-repeat;
          background-position: center;
          background-size: 195px 195px;
        }
        .osdtext {
          background: url('../../assets/source_icons/osdtext.png') no-repeat;
          background-position: center;
          background-size: 195px 195px;
        }
      }
    }
    .operators {
      height: 75px;
      width: 100%;
      position: fixed;
      bottom: 0;
      border-top: 1px solid #5c606f; /*no*/
      display: flex;
      .btn {
        flex: 1;
        height: 100%;
      }
      .edit {
        background: url('../../assets/common/edit-o.png') no-repeat;
        background-size: 28px 32px;
        background-position: center;
      }
      .del {
        background: url('../../assets/common/delete-o.png') no-repeat;
        background-size: 28px 32px;
        background-position: center;
      }
    }
  }
  @media screen and (orientation: landscape) {
    .add-box,
    .pic-box {
      width: 20%;
    }
  }
  @media screen and (orientation: portrait) {
    .add-box,
    .pic-box {
      width: 33.3%;
    }
  }
</style>

<style lang="less">
  .addtext {
    background: url('../../assets/common/text.png') no-repeat;
  }
  .addpic {
    background: url('../../assets/common/pic.png') no-repeat;
  }
  .mint-actionsheet {
    background: initial;
  }
  .mint-actionsheet-list,
  .mint-actionsheet-button {
    margin-left: 20px;
    margin-right: 20px;
  }
  .mint-actionsheet-button {
    width: auto;
  }
  .mint-actionsheet-listitem {
    color: #fff;
    margin-bottom: 10px;
    border-radius: 20px;
    height: 100px;
    line-height: 100px;
  }
</style>






